/**
 *
 * @author dieter <dieter@taotesting.com>
 * @author Jean-Sbastien Conan <jean-sebastien.conan@vesperiagroup.com>
 */
define('taoClientDiagnostic/controller/CompatibilityChecker/diagnostics',[
    'jquery',
    'layout/loading-bar',
    'taoClientDiagnostic/tools/diagnostic/diagnostic',
    'ui/feedback'
], function ($, loadingBar, diagnosticFactory, feedback) {
    'use strict';

    /**
     * @exports
     */
    return {
        start: function(config) {
            var $feedbackBox = $('#feedback-box');
            var $contentArea = $('.content');

            if(!!$feedbackBox.data('error')){
                feedback().error($feedbackBox.data('error'));
            }
            if($feedbackBox.data('message')){
                feedback().error($feedbackBox.data('message'));
            }

            diagnosticFactory(config)
                .on('start', function() {
                    loadingBar.start();
                })
                .on('end', function() {
                    loadingBar.stop();
                })
                .render($contentArea);
        }
    };
});

